          <div class="col-sm-12 mt-4" style="height:18px;font-size:18px;font-family:PingFang-SC-Bold;font-weight:bold;color:rgba(255,255,255,1);"><img :src="baseInfo.logoPath" style="border-radius: 50%;width: 51px;height: 51px;" v-if="baseInfo && baseInfo.logoPath"><span style="margin-left: 15px" v-if="baseInfo && baseInfo.teamName">{{baseInfo.teamName}} | {{baseInfo.projectName}}</span></div>
          <div class="col-sm-12 mt-6" style="font-family:PingFang-SC-Regular;font-weight:bold;color:rgba(255,255,255,1);">
            <span v-if="baseInfo && baseInfo.modeName">{{baseInfo.modeName}} |</span>
            <span v-if="baseInfo && baseInfo.memberName">成员  {{baseInfo.memberName}}  |</span>
            <span v-if="baseInfo && baseInfo.currentStageName && baseInfo.stepName">当前进度  {{baseInfo.currentStageName}}-{{baseInfo.stepName}} </span>
          </div>
        </div>
      </div>
      <div class="row" style="background:rgba(255,255,255,1);box-shadow:0px 3px 15px 0px rgba(0, 0, 0, 0.08);height: 59px;line-height: 59px">
        <div class="col-md-3 col-md-offset-1"  style="cursor: pointer">
          <span @click="goBack(-1)">助教中心</span> > 查看日报
        </div>
      </div>
      <div class="row pt-4 mt-3">
        <div class="col-md-7 col-md-offset-1" v-show="showAllDaily==1" style="text-align: left;">
          <div class="dateTitle">
            <span class="dateTitleFont">{{selectedDay}}</span>
          </div>
          <div class="d4"></div>
          <el-timeline style="margin-top: 20px">
            <el-timeline-item :timestamp="item.createTime" placement="top" v-for="item in dailyList" v-bind:key="item.id">
              <el-card>
                <div class="daily row">
                  <div class="daily-img col-lg-1">
                    <img class="daily-img-item" :src="item.head"/>
                  </div>
                  <div class="daily-name">
                    {{item.userName}}
                  </div>
                  <!--                      <div class="daily-time">用时：{{item.time}}小时</div>-->
                  <div class="col-lg-11">
                    <div class="daily-title row">工作内容：</div>
                    <div class="daily-content">
                      {{item.content}}
                    </div>
                    <!--                        <div class="daily-title row" v-show="clickStepShow==true">明日工作计划：</div>-->
                    <!--                        <div class="daily-content" v-show="clickStepShow==true">-->
                    <!--                          {{item.tomorrow}}-->
                    <!--                        </div>-->
                  </div>
                  <!--                      <div class="showAll" @click="changeclickStepShow">-->
                  <!--                        <span style="width:9px;height:5px;font-size:14px;font-family:PingFang-SC-Regular;font-weight:400;color:rgba(102,102,102,1);"></span><span class="showAllItem" v-show="clickStepShow==true">收起</span>-->
                  <!--                        <span style="width:9px;height:5px;font-size:14px;font-family:PingFang-SC-Regular;font-weight:400;color:rgba(102,102,102,1);"></span><span class="showAllItem" v-show="clickStepShow==false">展开</span>-->
                  <!--                      </div>-->
