          <div class="dateTitle">
            <span class="dateTitleFont">{{selectedDay}}</span>
          </div>
          <div class="d4"></div>
          <div class="no-report">该日未提交日报</div>
        </div>
        <div class="col-md-4  task-list pr-0 pl-0">
          <el-calendar v-model="calendarValue" :dailyStatusTaskList="reportDaysList" @monthChange="changeMonth" @input="dayInput" v-if="calendarflg">
          </el-calendar>
        </div>
      </div>
      </div>
    </div>
</template>

<script>
import Header from '../../../components/assistant/Header.vue'
import getQueryString from '@/utils/getQueryString'
import getDateStr from '@/utils/getDateStr'
export default {
  name: 'app',
  components: {
    Header
  },
  mounted () {
    this.loadData()
  },
  data () {
    return {
      baseInfo: null,
      teamId: '',
      projectId: '',
      currentStepId: -1,
      facultyMarjorId: '',
      calendarValue: '',
      showAllDaily: 0,
      dailyList: [],
      reportDaysList: [], // 该月哪天提交了日报
      calendarflg: false,
      selectedDay: ''
    }
  },
  methods: {
    loadData () {
      let me = this
      me.teamId = parseInt(getQueryString('teamId'))
      me.projectId = parseInt(getQueryString('projectId'))
      me.currentStepId = parseInt(getQueryString('currentStepId'))
      me.facultyMarjorId = parseInt(getQueryString('facultyMarjorId'))
      var today = getDateStr(0) // 获取当前日期
      me.selectedDay = today
      me.loadReportDays(today)
      me.loadReport(today)
      me.$http.post('/assistant/selectTeamProgressInfo', { teamId: me.teamId, currentStepId: me.currentStepId, projectId: me.projectId }, res => {
        if (res.success) {
          if (res.data.mode === 0) {
            res.data.modeName = '单人模式'
          } else {
            res.data.modeName = '团队模式'
          }
          me.baseInfo = res.data
          localStorage.setItem('assTeamInfo', JSON.stringify(res.data))
        }
      })
    },
    changeMonth (month) {
      this.reportDaysList = []
      this.calendarflg = false
      let dateStr = month + '-01'
      this.loadReportDays(dateStr)
    },
    dayInput (day) {
      if (day > getDateStr(0)) {
        this.$message.error('不能查看今天之后的内容！')
        return false
      }
      this.loadReport(day)
    },
    loadReportDays (dateStr) {
      let me = this
      me.$http.post('/doproject/dailyreport/reporteddays', { 'teamid': me.teamId, 'dateStr': dateStr }, res => {
        if (res.success && res.data.length > 0) {
          for (var i = 0; i < res.data.length; i++) {
            me.reportDaysList.push(parseInt(res.data[i].substr(8, 2)))
          }
        } else {
        }
        me.calendarflg = true
      })
    },
    goBack (index) {
      window.history.go(index)
    },
    loadReport (dateStr) {
      let me = this
      me.$http.post('/doproject/dailyreport/detail', { 'teamid': me.teamId, 'date': dateStr }, res => {
        if (res.success) {
          if (res.data.length > 0) {
            me.dailyList = res.data
            me.selectedDay = dateStr
            me.showAllDaily = 1
          } else if (dateStr !== getDateStr(0)) {
            me.showAllDaily = 2
            me.selectedDay = dateStr
          } else {
            me.showAllDaily = 2
          }
        }
      })
    }
  }
}
</script>
